// Prisma Client Singleton for Next.js
// Detta f√∂rhindrar flera instanser i development med hot reload

const { PrismaClient } = require('@prisma/client');

let prisma;

if (process.env.NODE_ENV === 'production') {
  prisma = new PrismaClient({
    log: ['error', 'warn'],
  });
} else {
  // I development, anv√§nd global f√∂r att √∂verleva hot reload
  if (!global.prisma) {
    global.prisma = new PrismaClient({
      log: ['query', 'error', 'warn'],
    });
  }
  prisma = global.prisma;
}

// Kontrollera om databas √§r aktiverad
const isDatabaseEnabled = process.env.ENABLE_DATABASE === 'true';

// Exportera b√•de client och status
module.exports = {
  prisma: isDatabaseEnabled ? prisma : null,
  isDatabaseEnabled
};